.footer {
  background-color: #282c34;
  padding: 10px;
  color: #ff6347;
  text-align: right;
  position: fixed;
  width: 100%;
  bottom: 0;
}

.footer .links {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.footer a {
  color: #61dafb;
  margin: 0 10px;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}
